		}, 400)
	} */
	onLoad() {
		this.windowHeight = uni.getSystemInfoSync().windowHeight
	},
	onShow() {
		if (!this.show) {
			this.show = true
			setTimeout(() => {
				this.load()
			}, 100)
		}
	},
	onPageScroll() {
		this.load()
	}
